.button {
  color: #fff;
  text-align: center;
  cursor: pointer;
  letter-spacing: .06em;
  text-transform: uppercase;
  background-color: #3c57a6;
  border: none;
  border-radius: 3px;
  justify-self: center;
  padding: 5px 20px;
  font-family: Unbounded, cursive;
  font-size: 18px;
  font-weight: 700;
  line-height: 1.8;
  text-decoration: none;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
